import React, {useRef} from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { Parallax } from 'react-scroll-parallax'
import Image from '../components/Image'
import useWindowDimensions from './useWindowDimensions'
import {useSpring, useChain, animated} from 'react-spring'

const styles = theme => ({
  root: {
    color: theme.palette.primary.text,
    backgroundColor: theme.palette.primary.main,
    position: 'relative',
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justify: 'center',
    height: '100vh',
  },
  typography: {
    color: theme.palette.primary.text,
    fontFamily: 'Simple Snails',
  },
  container: {
    display: 'flex',
    alignItems: 'flex-start',
    justify: 'center',
    flexDirection: 'column',
    height: '200px'
  },
  grid: {
    width: '100vw',
    overflowX: 'hidden'
  },
  body: {
    color: theme.palette.primary.text,
    margin: theme.spacing(2)
  },
  paper: {
    display: 'absolute',
    width: '50vw',
    backgroundColor: 'transparent'
  },
  img:{
    height: '100px',
    width: '100px'
  },
  filler: {
    display: 'fixed',
    height: '64px'
  }
})

const AboutSection = React.forwardRef((props, ref) => {
  const { classes } = props
  const { width } = useWindowDimensions();

  const introRef = useRef()
  const nameRef = useRef()

  const introAnim = useSpring({opacity: 1, from: {opacity: 0}, config: { duration: 1000 }, ref: introRef})
  const nameAnim = useSpring({opacity: 1, from: {opacity: 0}, config: { duration: 3000 }, ref: nameRef})

  useChain([introRef, nameRef])

  return (
    <section className={classes.root} ref={ref}>
      <Grid container className={classes.grid} spacing={0}  alignItems='center' justify='center'>
        <Grid item xs={6}>
          <Parallax  x={[width/2.5 + 'px', -width/2 + 'px']}>
            <Image src="background.jpg" className={classes.img}/>
          </Parallax>
        </Grid>

        <Grid item xs={6}>
          <Parallax x={[-width/2.5  + 'px', width/2 + 'px']} >
            <Typography variant="h5" color="textPrimary" align="left" className={classes.typography}>
                <animated.div style={introAnim}>{'Hey, I\'m'}</animated.div>
            </Typography>
            <Typography variant="h2" color="textPrimary" align="left" className={classes.typography}>
                <animated.div style={nameAnim}>{'Thomas'}</animated.div>
            </Typography>
          </Parallax>
        </Grid>
        <Grid item xs={12}>
          <div className={classes.filler}></div>
        </Grid>
      </Grid>
    </section>
  )
})

AboutSection.displayName = 'AboutSection'

AboutSection.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(AboutSection)
