import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'

const backgroundImage = 'background2.jpg'

const styles = theme => ({
  root: {
    color: theme.palette.common.black,
    position: 'relative',
    display: 'flex',
    alignItems: 'center'
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  backdrop: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.palette.common.white,
    opacity: 1,
    zIndex: -11
  },
  background: {
    backgroundImage: `url(${backgroundImage})`,
    backgroundColor: theme.palette.common.black,
    backgroundPosition: 'center',
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundAttachment: 'fixed',
    zIndex: -12

  },
  arrowDown: {
    position: 'absolute',
    bottom: theme.spacing(4)
  }
})

const AboutSectionLayout = React.forwardRef((props, ref) => {
  const { children, classes } = props
  return (
    <section className={classes.root} ref={ref}>
      <Container className={classes.container}>
        {children}
        <div className={classes.backdrop} />
        <div className={classes.background}/>
      </Container>
    </section>
  )
})

AboutSectionLayout.displayName = 'AppLayout'

AboutSectionLayout.propTypes = {
  children: PropTypes.node.isRequired,
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(AboutSectionLayout)
